<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示店铺的所有营销活动信息</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>活动名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>活动时间</label>
        <cms-picker v-model="queryParams.timer" @change="setTimer" ></cms-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
     <section class="cms-batch">
        <cms-button icon="add" type="success" size="small" @click="$routerLink('/storeActivity/save','save')">新增</cms-button>
        <cms-button icon="error" type="error" size="small" @click="deleteDatas(ids)">批量删除</cms-button>
      </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="name" label="活动名称" min-width="220"></el-table-column>
      <el-table-column prop="discountAmount" label="活动时间" min-width="220">
        <div slot-scope="scope">
        {{$moment(scope.row.promoteBeginTime,'YYYY-MM-DD')}} 至 {{$moment(scope.row.promoteEndTime,'YYYY-MM-DD')}}
        </div>
      </el-table-column>
      <el-table-column label="活动类型" min-width="120">
        <div slot-scope="scope">
            <span v-if="scope.row.promoteType==1">单品折扣</span>
           <span v-if="scope.row.promoteType==2">单品一口价</span>
            <span v-if="scope.row.promoteType==3">满减</span>
             <span v-if="scope.row.promoteType==4">优惠券</span>    
              <span v-if="scope.row.promoteType==5">满赠</span>
               <span v-if="scope.row.promoteType==6">包邮</span>
        </div>
      </el-table-column>
      <el-table-column label="操作选项" width='380' fixed="right" align="center">
        <div slot-scope="scope">
          <el-button type="text" @click="$routerLink('/storeActivity/edit','view',{id:scope.row.id})">修改</el-button>
          <el-button type="text" @click.stop="deleteData(scope.row.id)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
    <!-- 选择商品组件 -->
   
  </div>
</template>
<script>
import { activityPage, activityDelete } from './../../../../api/marketing/index.js'
import list from '@/mixins/list'
var defaultData = {
  name: '',
  beginTime: '',
  endTime: '',
  promoteType: '',
  size: '15',
  page: '1'
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    setTimer(value) {
      this.queryParams.beginTime = value[0]
      this.queryParams.endTime = value[1]
      this.getTableData(activityPage, this.queryParams)
    },
    deleteData(id) {
      this.deleteTableData(activityDelete, id)
    },
    deleteDatas(ids) {
      this.deleteTableDatas(activityDelete, ids)
    }

  },
  created() {
    this.getTableData(activityPage, this.queryParams)
  }
}
</script>

<style>
</style>



